import { FC } from 'react'
import { Dialog, Typography, Box, Divider, Button, IconButton } from '@material-ui/core'
import Style from '../../styles/components/Shared/CustomDialogMessage.module.scss'
import { ReactComponent as IconSuccess } from '../../assets/icons/message_success.svg'
import { ReactComponent as IconFaild } from '../../assets/icons/message_faild.svg'
import { ReactComponent as IconWarning } from '../../assets/icons/message_warning.svg'
import { useTranslation } from 'react-i18next'
import CloseIcon from '@material-ui/icons/Close'
import clsx from 'clsx'
interface IDialogProps {
  isOpen: boolean
  handleClose: () => void
  message?: string | null
  title: string | null
  isSuccess: 'success' | 'failed' | 'warning'
  handleAgain?: () => void
}
const CustomDialogMessage: FC<IDialogProps> = ({ isOpen, handleClose, title, message, isSuccess, handleAgain }) => {
  const { t } = useTranslation()
  return (
    <>
      <Dialog onClose={handleClose} open={isOpen} maxWidth="sm" scroll="body" classes={{ paper: Style.dialogWrapper }}>
        <IconButton className={Style.closeIcon} size="small" onClick={handleClose}>
          <CloseIcon />
        </IconButton>
        {isSuccess === 'success' ? <IconSuccess /> : isSuccess === 'failed' ? <IconFaild /> : <IconWarning />}

        <Box
          className={clsx(Style.success, {
            [Style.error]: isSuccess === 'failed',
            [Style.warning]: isSuccess === 'warning',
          })}
        >
          <Typography variant="h2">{title}</Typography>
        </Box>
        {message && (
          <Box className={Style.message}>
            <Typography>{message}</Typography>
          </Box>
        )}

        {isSuccess === 'warning' && (
          <>
            <Divider />
            <Box className={Style.action}>
              <Button color="secondary" variant="contained" onClick={handleAgain}>
                {t('Thc hin li')}
              </Button>
            </Box>
          </>
        )}
      </Dialog>
    </>
  )
}
export default CustomDialogMessage
