import { FC, useState } from 'react'
import { BrowserRouter, Redirect, Route, Switch } from 'react-router-dom'
import { pagePath } from './utils/constants/pagePath'
import { isMobileState } from './stores/sharedStores'
import { useRecoilValue } from 'recoil'

//public
import FAQ from './pages/FAQ'

//landing
import HomeLandingPage from './pages/LandingPage/HomeLandingPage'
import LayoutLandingPage from './layouts/LayoutLandingPage'
import ProductsPage from './pages/LandingPage/ProductsPage'
import ProductDetailPage from './pages/LandingPage/ProductDetailPage'
import CompareProducts from './pages/LandingPage/CompareProducts'
import Promotion from './pages/LandingPage/Promotion'
import DetailPromotion from './pages/LandingPage/DetailPromotion'
import CustomerJourney from './pages/LandingPage/CustomerJourney'
import NewsPage from './pages/LandingPage/NewsPage'
import Search from './pages/LandingPage/Search'
import ScrollToTop from './config/scrollToTop'
import SitemapPage from './pages/LandingPage/SitemapPage'
import NewsDetailPage from './pages/LandingPage/NewsDetailPage'
import Feedback from './pages/LandingPage/Feedback'

const Router: FC = () => {
  const [isAuthenticated] = useState(false)
  const isMobile = useRecoilValue(isMobileState)

  const defaultLayoutRoutes = (fromPath: string) => {
    return (
      <Redirect
        to={{
          pathname: pagePath.error404,
          state: { from: fromPath },
        }}
      />
    )
  }

  const landingPageRouter = [
    <Route key="product_detail" path={pagePath.productDetail} component={ProductDetailPage} exact />,
    <Route key="products_page" path={pagePath.productPage} component={ProductsPage} exact />,
    <Route key="compare_products" path={pagePath.compareProductsPage} component={CompareProducts} exact />,
    <Route key="promotion" path={pagePath.promotion} component={Promotion} exact />,
    <Route key="detail_promotion" path={pagePath.detailpromotion} component={DetailPromotion} exact />,
    <Route key="customer-journey" path={pagePath.customerJourney} component={CustomerJourney} exact />,
    <Route key="faq_page" path={pagePath.faq} component={FAQ} exact={true} />,
    <Route key="news_page" path={pagePath.news} component={NewsPage} exact />,
    <Route key="search" path={pagePath.search} component={Search} exact />,
    <Route key="sitemap_page" path={pagePath.sitemap} component={SitemapPage} exact />,
    <Route key="newsDetail_page" path={pagePath.newsDetail} component={NewsDetailPage} exact />,
    <Route key="feedback" path={pagePath.feedback} component={Feedback} exact />,
    <Route key="landing_page" component={HomeLandingPage} />,
  ]

  const renderLandingPageLayout = () => {
    return (
      <LayoutLandingPage>
        <Switch>{landingPageRouter}</Switch>
      </LayoutLandingPage>
    )
  }

  return (
    <BrowserRouter>
      <ScrollToTop />
      {renderLandingPageLayout()}
    </BrowserRouter>
  )
}

export default Router
