import clsx from 'clsx'
import { FC } from 'react'
import Style from '../../styles/components/Shared/CustomStepperHorizontal.module.scss'

interface ICustomStepperHorizontal {
  active: number
  steps: number
}
const CustomStepperHorizontal: FC<ICustomStepperHorizontal> = (props) => {
  const { active, steps } = props
  return (
    <>
      {Array(steps)
        .fill(null)
        .map((e, index) => {
          return (
            <div
              key={index}
              className={clsx(Style.item, {
                [Style.active]: index < active,
              })}
            ></div>
          )
        })}
    </>
  )
}

export default CustomStepperHorizontal
