import { Box, Button, Dialog, IconButton, Typography } from '@material-ui/core'
import { FC, useState } from 'react'
import Style from '../../styles/components/Login/ForgotPassword.module.scss'
import CloseIcon from '@material-ui/icons/Close'
import Logo from '../../assets/Logo-2.png'
import { useTranslation } from 'react-i18next'
import CustomStepperHorizontal from '../Shared/CustomStepperHorizontal'
import CustomTextField from '../Shared/CustomTextField'
import Joi from 'joi'
import { useValidateForm } from '../../hooks/useValidateForm'
import ReCAPTCHA from 'react-google-recaptcha'
import { CAPTCHA_SITE_KEY } from '../../config/common'

interface IForgotPassword {
  isOpen: boolean
  handleClose: () => void
}

const schema = Joi.object({
  phoneNumber: Joi.string()
    .empty(null)
    .required()
    .pattern(new RegExp(/(0[3|5|7|8|9])+([0-9]{8})\b/))
    .messages({
      'any.required': 'phoneNotBlank',
      'string.pattern.base': 'phoneNotValid',
    }),
})

const ForgotPassword: FC<IForgotPassword> = (props) => {
  const { isOpen, handleClose } = props
  const { t } = useTranslation()
  const [activeStep, setActiveStep] = useState<number>(1)
  const [validateKey, setValidateKey] = useState<string>()
  const [disabledSubmit, setDisabledSubmit] = useState<boolean>(true)
  const [form, setForm] = useState({
    phoneNumber: null,
  })

  const { eraseErrors, validateAll, errors } = useValidateForm(form, schema, validateKey)

  const handleChange = (value, key) => {
    setValidateKey(key)
    setForm({
      ...form,
      [key]: value,
    })
  }

  const handleSubmit = () => {
    if (validateAll()) {
      alert('submit')
    }
  }

  const getErrorMessage = (field: string) => {
    const error = errors?.find((f) => f.field === field)
    return error ? t(error.message) : ''
  }

  const hasErrors = (field: string) => {
    const error = getErrorMessage(field)
    return !!error
  }

  const handleDisabledSubmit = () => {
    if (form && validateAll()) {
      setDisabledSubmit(false)
    } else {
      setDisabledSubmit(true)
    }
  }

  const onChangeReCaptcha = (value) => {
    alert(value)
  }

  return (
    <Dialog onClose={handleClose} open={isOpen} maxWidth="sm" scroll="body" classes={{ paper: Style.dialogWrapper }}>
      <Box textAlign="right">
        <IconButton className={Style.closeIcon} size="small" onClick={handleClose}>
          <CloseIcon />
        </IconButton>
      </Box>
      <Box textAlign="center">
        <img src={Logo} alt="HDBank" />
      </Box>
      <Box my={3}>
        <Typography variant="h2" color="primary">
          {t('Cp li mt khu')}
        </Typography>
      </Box>
      <Box textAlign="center">
        <CustomStepperHorizontal steps={4} active={activeStep} />
      </Box>
      <Box mt={3}>
        <CustomTextField
          value={form.phoneNumber}
          placeholder={`${t('phoneNumber')} *`}
          onChange={(value) => handleChange(value || null, 'phoneNumber')}
          error={hasErrors('phoneNumber')}
          helperText={getErrorMessage('phoneNumber')}
          onKeyUp={() => handleDisabledSubmit()}
        />
      </Box>
      <Typography variant="body2" className={Style.note}>
        {t('Nhp s in thoi bn  ng k vi HDBank.')}
      </Typography>

      <Box mt={3}>
        <ReCAPTCHA sitekey={CAPTCHA_SITE_KEY} onChange={onChangeReCaptcha} />
      </Box>
      <Box mt={3}>
        <Button
          variant="contained"
          color="primary"
          disabled={disabledSubmit}
          fullWidth={true}
          onClick={() => handleSubmit()}
        >
          {t('next')}
        </Button>
      </Box>
    </Dialog>
  )
}

export default ForgotPassword
