import Style from '../../styles/components/Shared/Tabs.module.scss'
import { Typography } from '@material-ui/core'
import { FC, useMemo, useState } from 'react'
import { useLocation } from 'react-router'
import clsx from 'clsx'
interface ITabItem {
  label: string
  value: string | null
}
interface ITabsProps {
  tabs: ITabItem[]
  onChange: (value) => void
  value?: string | null
}
const Tabs: FC<ITabsProps> = ({ tabs, onChange, value }) => {
  const [active, setActive] = useState(value || null)
  const handleChange = (event, value) => {
    setActive(value)
    onChange(value)
  }
  return (
    <nav className={Style.tabs}>
      <ul>
        {tabs.map((tab, index) => {
          return (
            <li
              key={index}
              className={clsx('', {
                [Style.active]: active === tab.value,
              })}
              onClick={(e) => handleChange(e, tab.value)}
            >
              <Typography component="span">{tab.label}</Typography>
            </li>
          )
        })}
      </ul>
    </nav>
  )
}

const useQueryParams = () => {
  const { search } = useLocation()
  return useMemo(() => new URLSearchParams(search), [search])
}

export default Tabs
