import { Box, Container, Grid, Typography } from '@material-ui/core'
import { FC, useState, useEffect } from 'react'
import Style from '../../styles/pages/LandingPage/Sitemap.module.scss'
import { useTranslation } from 'react-i18next'

import { getSitemap } from '../../apis/landing-page/sitemap'
import { useQuery } from 'react-query'

const SitemapPage: FC = () => {
  const { t } = useTranslation()

  const [content, setContent] = useState([])

  const { data, isFetching } = useQuery('sitemapQuery', getSitemap, {})

  useEffect(() => {
    if (!data) return
    setContent(data)
  }, [data])
  if (isFetching) return <></>

  return (
    <Container className={Style.sitemapPageWrap}>
      <Box py={3}>
        <Typography variant="h4">{t('S  trang')}</Typography>
      </Box>

      <Grid className={Style.listWrap}>
        {content?.map((group, idx) => {
          const { title, itemsChild } = group
          const lstChild: any[] = itemsChild
          return (
            <Grid key={idx} className={Style.group}>
              <Typography variant="body1" className={Style.title}>
                {t(title)}
              </Typography>

              {lstChild?.map((item, idx) => {
                const { itemText } = item
                return (
                  <Grid key={idx}>
                    <span className={Style.link}>{t(itemText)}</span>
                  </Grid>
                )
              })}
            </Grid>
          )
        })}
      </Grid>
    </Container>
  )
}

export default SitemapPage
