import { Avatar, Box, Button, Dialog, IconButton, Typography } from '@material-ui/core'
import { FC, useEffect, useState } from 'react'
import Style from '../../styles/components/Login/DialogLogin.module.scss'
import CloseIcon from '@material-ui/icons/Close'
import Logo from '../../assets/Logo-2.png'
import CustomTextField from '../Shared/CustomTextField'
import { useTranslation } from 'react-i18next'
import { VisibilityRounded, VisibilityOffRounded } from '@material-ui/icons'
import { Link } from 'react-router-dom'
import ReCAPTCHA from 'react-google-recaptcha'
import CustomDialogMessage from '../Shared/CustomDialogMessage'
import { useValidateForm } from '../../hooks/useValidateForm'
import Joi from 'joi'
import ForgotPassword from './ForgotPassword'
import { CAPTCHA_SITE_KEY } from '../../config/common'

interface IDialogLogin {
  isOpen: boolean
  handleClose: () => void
}

const schema = Joi.object({
  username: Joi.string()
    .empty(null)
    .required()
    .min(6)
    .max(20)
    .pattern(new RegExp(/^[a-zA-Z0-9]+$/))
    .messages({
      'any.required': 'usernameNotBlank',
      'string.min': 'usernameMinLength',
      'string.max': 'usernameMaxLength',
      'string.pattern.base': 'usernameNotValid',
    }),
  password: Joi.string().empty(null).required().min(6).max(20).messages({
    'any.required': 'passwordNotBlank',
    'string.min': 'passwordMinLength',
    'string.max': 'passwordMaxLength',
  }),
})

const DialogLogin: FC<IDialogLogin> = (props) => {
  const { isOpen, handleClose } = props
  const { t } = useTranslation()
  const [isLogin, setIsLogin] = useState<boolean>(false)
  const [form, setForm] = useState({
    username: null,
    password: null,
  })
  const [hiddenPassword, setHiddenPassword] = useState<boolean>(true)
  const [isMessage, setIsMessage] = useState<boolean>(false)
  const [validateKey, setValidateKey] = useState<string>()
  const [disabledSubmit, setDisabledSubmit] = useState<boolean>(true)
  const [isForgotPassword, setIsForgotPassword] = useState<boolean>(false)

  const { errors, validateAll, eraseErrors } = useValidateForm(form, schema, validateKey)

  const handleChange = (value, key) => {
    setValidateKey(key)
    setForm({
      ...form,
      [key]: value,
    })
  }

  const onChangeReCaptcha = (value) => {
    alert(value)
  }

  const handleCloseMessage = () => {
    setIsMessage(!isMessage)
  }

  const handleSubmit = () => {
    if (validateAll()) {
      handleCloseMessage()
    }
  }

  const getErrorMessage = (field: string) => {
    const error = errors?.find((f) => f.field === field)
    return error ? t(error.message) : ''
  }

  const hasErrors = (field: string) => {
    const error = getErrorMessage(field)
    return !!error
  }

  const handleCloseDialog = () => {
    eraseErrors()
    setDisabledSubmit(true)
    setForm({
      username: null,
      password: null,
    })
    handleClose()
  }

  const handleForgotPassword = () => {
    handleCloseDialog()
    setIsForgotPassword(!isForgotPassword)
  }

  const handleCloseForgotPassword = () => {
    setIsForgotPassword(!isForgotPassword)
  }

  const disableSubmit = () => {
    if (form.username && form.password && validateAll()) {
      setDisabledSubmit(false)
    } else {
      setDisabledSubmit(true)
    }
  }

  const renderDialogMessage = () => {
    return (
      <CustomDialogMessage
        isOpen={isMessage}
        handleClose={() => handleCloseMessage()}
        title="ng nhp  khng thnh cng"
        isSuccess="warning"
        message="Tn ng nhp hoc mt khu khng ng. Vui lng th li. Ti khon ca bn s b kha nu nhp sai mt khu lin tip 5 ln"
      />
    )
  }

  const renderForgotPassword = () => {
    return <ForgotPassword isOpen={isForgotPassword} handleClose={() => handleCloseForgotPassword()} />
  }

  return (
    <>
      <Dialog
        onClose={handleCloseDialog}
        open={isOpen}
        maxWidth="sm"
        scroll="body"
        classes={{ paper: Style.dialogWrapper }}
      >
        <IconButton className={Style.closeIcon} size="small" onClick={handleCloseDialog}>
          <CloseIcon />
        </IconButton>
        <Box textAlign="center">
          <img src={Logo} alt="HDBank" />
        </Box>
        <Box my={3}>
          {isLogin ? (
            <div className={Style.avatarWrapper}>
              <Avatar
                alt="Remy Sharp"
                src="https://mui.com/static/images/avatar/1.jpg"
                className={Style.avatarImg}
              ></Avatar>
              <span>Nguyn Phm Thu Anh</span>
            </div>
          ) : (
            <CustomTextField
              value={form.username}
              placeholder={`${t('username')} *`}
              onChange={(value) => handleChange(value || null, 'username')}
              error={hasErrors('username')}
              helperText={getErrorMessage('username')}
              onKeyUp={() => disableSubmit()}
            />
          )}

          <CustomTextField
            value={form.password}
            placeholder={`${t('password')} *`}
            onChange={(value) => handleChange(value || null, 'password')}
            icon={hiddenPassword ? <VisibilityOffRounded /> : <VisibilityRounded />}
            isIconClick={true}
            onIconClick={() => setHiddenPassword(!hiddenPassword)}
            type={hiddenPassword ? 'password' : 'text'}
            error={hasErrors('password')}
            helperText={getErrorMessage('password')}
            onKeyUp={() => disableSubmit()}
            onCopy={(e) => {
              e.preventDefault()
              return false
            }}
            onPaste={(e) => {
              e.preventDefault()
              return false
            }}
          />
        </Box>
        <ReCAPTCHA sitekey={CAPTCHA_SITE_KEY} onChange={onChangeReCaptcha} />
        <Box mt={3}>
          <Button
            variant="contained"
            fullWidth={true}
            color="primary"
            disabled={disabledSubmit}
            onClick={() => handleSubmit()}
          >
            {t('login')}
          </Button>
        </Box>
        <Box textAlign="center" mt={2} mb={2}>
          <Link to="#" className={Style.forgotPassword} onClick={() => handleForgotPassword()}>
            {t('forgotPassword')}
          </Link>
        </Box>
        <Typography>
          {t('notAccount')}{' '}
          <Link to="#">
            <Typography color="secondary">{t('registerNow')}</Typography>
          </Link>
        </Typography>
      </Dialog>
      {renderDialogMessage()}
      {renderForgotPassword()}
    </>
  )
}

export default DialogLogin
