import { Box, Grid, Typography } from '@material-ui/core'
import { FC, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import Style from '../../styles/components/Shared/SocialShare.module.scss'
import { FacebookShareButton, LinkedinShareButton, TwitterShareButton, TelegramShareButton } from 'react-share'
import FBIcon from '../../assets/socials/facebook.png'
import ZaloIcon from '../../assets/socials/zalo_40x40.png'
import LinkedIcon from '../../assets/socials/LinkedIn.png'
import TWIcon from '../../assets/socials/Twitter.png'
import TeleIcon from '../../assets/socials/Telegram.png'

const SocialShare: FC = () => {
  const { t } = useTranslation()

  useEffect(() => {
    const script = document.createElement('script')
    script.src = 'https://sp.zalo.me/plugins/sdk.js'
    script.async = true

    document.body.appendChild(script)

    return () => {
      document.body.removeChild(script)
    }
  }, [])

  return (
    <Grid container>
      <Grid item>
        <Box mb={1}>
          <Typography>
            <strong>{t('share')}</strong>
          </Typography>
        </Box>

        <Grid container justifyContent="space-between" spacing={2} className={Style.social}>
          <Grid item>
            <a href="/" target="_blank">
              <FacebookShareButton url={window.location.href}>
                <img src={FBIcon} alt="" />
              </FacebookShareButton>
            </a>
          </Grid>
          <Grid item>
            <button
              data-href={window.location.href}
              data-oaid="1148028551726591727"
              className="zalo-share-button"
              data-customize="true"
              data-layout="4"
            >
              <img src={ZaloIcon} alt="" />
            </button>
          </Grid>
          <Grid item>
            <LinkedinShareButton url={window.location.href}>
              <img src={LinkedIcon} alt="Linked" />
            </LinkedinShareButton>
          </Grid>
          <Grid item>
            <TwitterShareButton url={window.location.href}>
              <img src={TWIcon} alt="Twitter" />
            </TwitterShareButton>
          </Grid>
          <Grid item>
            <TelegramShareButton url={window.location.href}>
              <img src={TeleIcon} alt="Telegram" />
            </TelegramShareButton>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}

export default SocialShare
