import { FC, useState, useRef, useLayoutEffect, useEffect } from 'react'
import Style from '../../styles/components/Shared/Header.module.scss'
import {
  Button,
  Grid,
  Avatar,
  Box,
  Typography,
  Container,
  Popper,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  IconButton,
} from '@material-ui/core'
import { Link, useParams } from 'react-router-dom'
import { pagePath } from '../../utils/constants/pagePath'
import { useTranslation } from 'react-i18next'
import { useHistory, useLocation } from 'react-router'
import { useRecoilValue } from 'recoil'
import useOnClickOutside from '../../hooks/useOnClickOutSide'
import clsx from 'clsx'
import { getSuggestion } from '../../apis/landing-page/search'
import { useQuery } from 'react-query'

//component
import CustomTextField from './CustomTextField'
import DialogContact from '../Header/DialogContact'
import { isMobileState, homeContactRef } from '../../stores/sharedStores'

//icons
import SearchIcon from '@material-ui/icons/Search'
import AccessTime from '@material-ui/icons/AccessTime'
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight'
import CloseIcon from '@material-ui/icons/Close'
import { TrendingUpOutlined } from '@material-ui/icons'

import { ReactComponent as MenuIcon } from '../../assets/icons/menu.svg'
import { ReactComponent as FaqIcon } from '../../assets/icons/faq-outline.svg'
import { ReactComponent as SearchMenuIcon } from '../../assets/icons/search.svg'
import { ReactComponent as HomeIcon } from '../../assets/icons/home_outline.svg'
import { ReactComponent as CardIcon } from '../../assets/icons/card-outline.svg'
import { ReactComponent as GiftIcon } from '../../assets/icons/gift-outline.svg'
import { ReactComponent as NewsIcon } from '../../assets/icons/news-outline.svg'
import { ReactComponent as CardForYouIcon } from '../../assets/icons/card-for-you.svg'
import { ReactComponent as ContactIcon } from '../../assets/icons/email-outline.svg'

//image
import LogoMobile from '../../assets/Logo-2.png'
import Logo from '../../assets/Logo.png'
import { getCookie } from '../../utils/helpers/cookieHelpers'
import DialogLogin from '../Login/DialogLogin'

const menus = [
  {
    label: 'home',
    icon: <HomeIcon />,
    path: pagePath.landingPage,
  },
  {
    label: 'product',
    icon: <CardIcon />,
    path: pagePath.productPage,
  },
  {
    label: 'promotion',
    icon: <GiftIcon />,
    path: pagePath.promotion,
  },
  {
    label: 'news',
    icon: <NewsIcon />,
    path: pagePath.news,
  },
  {
    label: 'cardForYou',
    icon: <CardForYouIcon />,
    path: pagePath.customerJourney,
  },
  {
    label: 'contact',
    icon: <ContactIcon />,
    path: 'contact',
    isContact: true,
  },
  {
    label: 'FAQ',
    icon: <FaqIcon />,
    path: pagePath.faq,
  },
]

const Header: FC = () => {
  const cookieName = 'searchValue'
  const history = useHistory()
  const location = useLocation()
  const offsetTop = useRecoilValue(homeContactRef)
  const inputSearchRef = useRef<HTMLDivElement>(null)
  const [isLogin] = useState(false)
  const [activeMenu, setActiveMenu] = useState<string>()
  const [valueSearch, setValueSearch] = useState<string | null>(null)
  const [isOpenSuggest, setIsOpenSuggest] = useState<boolean>(false)
  const [anchorElMenu, setAnchorElMenu] = useState(null)
  const [searchBox, setSearchBox] = useState<boolean>(false)
  const [searchHistory, setSearchHistory] = useState<any>([])
  const [isOpen, setIsOpen] = useState(false)
  const [isDialogRegister, setIsDialogRegister] = useState<boolean>(false)
  const [isDialogLogin, setIsDialogLogin] = useState<boolean>(false)

  const { data, isFetching } = useQuery('suggestionQuery', getSuggestion)

  const getNumb = (str) => {
    const newString = str.slice(0, 15)
    const numbStr = newString.split(cookieName).join('').split('=').join('').split('{').join('')
    const parseNumb = parseInt(numbStr)
    const numb = parseNumb > 9 ? 3 : parseNumb > 99 ? 4 : 2
    return numb
  }

  const handleGetValue = (d) => {
    setIsOpenSuggest(false)
    setValueSearch(d)
    history.push(pagePath.search + '?keyword=' + d)
  }

  useLayoutEffect(() => {
    const activePage = menus.find((f) => f.path === location.pathname)
    if (activePage) setActiveMenu(activePage.path)
  }, [location.pathname])

  const handleDialog = () => setIsOpen(!isOpen)
  const handleDialogRegister = () => setIsDialogRegister(!isDialogRegister)

  const openMenu = Boolean(anchorElMenu)
  const id = openMenu ? 'simple-popper' : undefined
  useOnClickOutside(inputSearchRef, () => setIsOpenSuggest(false))
  const { t, i18n } = useTranslation()
  const isMobile = useRecoilValue(isMobileState)

  const changeLanguage = () => {
    i18n.changeLanguage(i18n.language === 'vn' ? 'en' : 'vn')
  }

  const handleShowMenu = (event) => {
    const anchorEl = event?.currentTarget?.closest('#headerMobile')
    setAnchorElMenu(anchorElMenu ? null : anchorEl)
  }

  const hadleSearchBox = (event) => {
    setSearchBox(!searchBox)
    if (isMobile) handleShowMenu(event)
  }

  const handleSubmitSearch = () => {
    if (valueSearch && valueSearch.trim() !== '') {
      setIsOpenSuggest(false)
      history.push(pagePath.search + '?keyword=' + valueSearch?.trim().normalize('NFC'))
      if (isMobile) {
        setAnchorElMenu(null)
        setSearchBox(!searchBox)
      }
    }
  }

  const handleSubmitSearchEnter = (event) => {
    if (event.key === 'Enter') {
      handleSubmitSearch()
    }
  }

  const onContactClick = () => {
    if (location.pathname === '/home' || location.pathname === '/') {
      offsetTop?.current.scrollIntoView(true)
      window.scrollBy({ top: -90, behavior: 'smooth' })
    } else {
      handleDialog()
    }
  }

  const mobileMenuClick = (item) => {
    if (item.isContact) {
      onContactClick()
    } else {
      history.push(item.path)
    }

    handleShowMenu({})
  }

  const handleOpenSuggest = () => {
    const temp: any[] = []
    for (let i = 0; i < 5; i++) {
      const cookieData = getCookie(`${cookieName}${i}`)
      if (cookieData) {
        temp.push(JSON.parse(cookieData))
      }
    }

    setSearchHistory(
      temp.sort((a, b) => {
        const timeA = new Date(a.time).getTime()
        const timeB = new Date(b.time).getTime()
        return timeB > timeA ? 1 : -1
      }),
    )
    setIsOpenSuggest(true)
  }

  const handleClose = () => {
    setIsDialogLogin(!isDialogLogin)
  }
  const renderMobileHeader = () => {
    return (
      <>
        <Container className={Style.headerMobile} id="headerMobile">
          <Grid container className={Style.wrapper} justifyContent="space-between" alignItems="center">
            {!searchBox ? (
              <>
                <Box onClick={handleShowMenu} aria-describedby="simple-popper">
                  {openMenu ? <CloseIcon color="primary" /> : <MenuIcon />}
                </Box>
                <img src={LogoMobile} alt="" />
                <Box onClick={hadleSearchBox}>{!openMenu && <SearchMenuIcon />}</Box>
              </>
            ) : (
              <>
                <Grid item>
                  <Box onClick={hadleSearchBox}>
                    <CloseIcon color="primary" />
                  </Box>
                </Grid>
                <Grid item xs={11}>
                  <CustomTextField
                    placeholder={t('search')}
                    value={valueSearch}
                    icon={<SearchIcon />}
                    onChange={(v) => setValueSearch(v)}
                    onFocus={handleOpenSuggest}
                    styleName={Style.searchBox}
                    isIconClick={true}
                    onIconClick={() => handleSubmitSearch()}
                    onKeyPress={(event) => handleSubmitSearchEnter(event)}
                  />
                </Grid>
              </>
            )}
          </Grid>
          <Popper
            id={id}
            open={openMenu}
            anchorEl={anchorElMenu}
            transition
            className={Style.popperMenu}
            popperOptions={{
              modifiers: {
                preventOverflow: {
                  padding: 0,
                  enabled: TrendingUpOutlined,
                  boundariesElement: 'scrollParent',
                },
                flip: {
                  enabled: false,
                },
                arrow: {
                  enabled: true,
                },
              },
            }}
          >
            {!searchBox ? renderMenu() : renderSuggestSearch()}
          </Popper>
        </Container>
      </>
    )
  }

  if (isFetching) return <></>

  const renderDialog = () => {
    const dProps = {
      isOpen,
      handleClose: () => handleDialog(),
    }
    return <DialogContact {...dProps} />
  }

  const renderMenu = () => {
    return (
      <Box className={Style.menuWrapper}>
        <Grid container className={Style.login} justifyContent="center" spacing={1}>
          <Grid item>
            <Button variant="contained" color="primary" disableElevation onClick={() => history.push('#')}>
              {t('loginNow')}
            </Button>
          </Grid>
          <Grid item>
            <Button
              variant="outlined"
              color="primary"
              className={Style.rightItem}
              onClick={() => history.push(pagePath.landingPage)}
            >
              {t('register')}
            </Button>
          </Grid>
        </Grid>
        <Box>
          <List component="nav" className={Style.listMenu}>
            <ListItem button divider className={Style.listItem}>
              <Grid container justifyContent="space-between">
                <Typography>{t('language')}</Typography>
                <Box display="flex" alignItems="center" onClick={changeLanguage}>
                  <Typography>{t('curentLanguage')}</Typography>
                  <KeyboardArrowRightIcon />
                </Box>
              </Grid>
            </ListItem>
            {menus.map((menu, idx) => {
              return (
                <ListItem
                  key={idx}
                  button
                  divider
                  className={clsx(Style.listItem, {
                    [Style.selected]: activeMenu === menu.path,
                  })}
                  onClick={() => mobileMenuClick(menu)}
                >
                  <ListItemIcon>{menu.icon}</ListItemIcon>
                  <ListItemText primary={t(menu.label)} />
                </ListItem>
              )
            })}

            <ListItem className={Style.listItem}>
              <Typography variant="body1">
                Hotline: <Typography color="primary">1900 6060</Typography>
              </Typography>
            </ListItem>
          </List>
        </Box>
      </Box>
    )
  }

  const renderSuggestSearch = () => {
    return (
      <Box className={Style.menuWrapper}>
        <SuggestSearch onClick={handleGetValue} suggestionList={data.suggestions} historySearch={searchHistory} />
      </Box>
    )
  }

  const renderPcHeader = () => {
    return (
      <Grid container className={Style.container}>
        <div className={`${isOpenSuggest && Style.overlay}`}></div>
        <Grid item sm={6} md={7}>
          <Grid container alignItems="center" className={Style.menuWrapper}>
            <Link to={pagePath.landingPage} className={Style.logo}>
              <img src={Logo} />
            </Link>
            <Link to={pagePath.productPage}>{t('product')}</Link>
            <Link to={pagePath.promotion}>{t('promotion')}</Link>
            <Link to={pagePath.news}>{t('news')}</Link>
            <Link to={pagePath.customerJourney}>{t('cardForYou')}</Link>
            <Link onClick={onContactClick}>{t('contact')}</Link>
            <Link to={pagePath.faq}>{t('FAQ')}</Link>
          </Grid>
        </Grid>
        <Grid item md={5}>
          <Grid container justifyContent="flex-end" alignItems="center" className={Style.profileWrapper}>
            <Grid item md={5}>
              {searchBox ? (
                <div ref={inputSearchRef}>
                  <CustomTextField
                    placeholder={t('search')}
                    value={valueSearch}
                    icon={<SearchIcon />}
                    onChange={(v) => setValueSearch(v)}
                    onFocus={handleOpenSuggest}
                    styleName={Style.searchBox}
                    isIconClick={true}
                    onIconClick={() => handleSubmitSearch()}
                    onKeyPress={(event) => handleSubmitSearchEnter(event)}
                  />
                  {isOpenSuggest && (
                    <SuggestSearch
                      onClick={handleGetValue}
                      suggestionList={data.suggestions}
                      historySearch={searchHistory}
                    />
                  )}
                </div>
              ) : (
                <Grid container justifyContent="flex-end">
                  <IconButton onClick={hadleSearchBox} className={Style.borderRound}>
                    <SearchIcon />
                  </IconButton>
                </Grid>
              )}
            </Grid>
            {isLogin || (
              <Link to={'#'} className={Style.login} onClick={() => handleClose()}>
                {t('login')}
              </Link>
            )}

            <Button variant="contained" color="primary" className={Style.rightItem} onClick={handleDialogRegister}>
              {t('register')}
            </Button>
            {isLogin && (
              <Avatar className={Style.avatar} alt="Remy Sharp" src="https://mui.com/static/images/avatar/1.jpg" />
            )}
          </Grid>
        </Grid>
      </Grid>
    )
  }

  const renderLogin = () => {
    const dialogLogin = {
      isOpen: isDialogLogin,
      handleClose: () => handleClose(),
    }
    return <DialogLogin {...dialogLogin} />
  }

  return (
    <>
      {isMobile ? renderMobileHeader() : renderPcHeader()}
      {renderDialog()}
      {renderLogin()}
    </>
  )
}

export default Header
const SuggestSearch = ({ onClick, suggestionList, historySearch }) => {
  const { t } = useTranslation()
  return (
    <Box className={Style.boxSuggest}>
      {historySearch && (
        <Box mb={2}>
          {historySearch.map((d, i) => (
            <Button key={i} variant="text" startIcon={<AccessTime />} onClick={() => onClick(d.value)}>
              {d.value}
            </Button>
          ))}
        </Box>
      )}

      <Box>
        <Typography component="p" color="primary">
          {t('keywordSuggestions')}
        </Typography>
        <Box mt={2}>
          <Grid container spacing={1}>
            {suggestionList.map((d, i) => (
              <Grid key={i} item>
                <Box onClick={() => onClick(d)} className={Style.suggetItem}>
                  {d}
                </Box>
              </Grid>
            ))}
          </Grid>
        </Box>
      </Box>
    </Box>
  )
}
