import { FC, useState, useEffect } from 'react'
import { getHomePage } from '../../apis/landing-page/home'
import { useQuery } from 'react-query'
import { useTranslation } from 'react-i18next'

//components
import Introduce from './Home/Introduce'
import Product from './Home/Product'
import SpecialOffer from './Home/SpecialOffer'
import Features from './Home/Features'
import CustomerReview from './Home/CustomerReview'
import Contact from './Home/Contact'
import News from './Home/News'
import DialogAdvertise from '../../components/LandingPage/DialogAdvertise'

const HomeLandingPage: FC = () => {
  const { i18n } = useTranslation()
  const langCode =
    i18n.language === 'vn' || i18n.language === 'vi-VN' ? 'vi' : i18n.language === 'en-US' ? 'en' : i18n.language

  const [content, setContent] = useState({
    top_feedback: [],
    top_news: [],
    top_products: [],
    top_promotes: [],
  })

  const { data, isFetching } = useQuery(['homePageQuery', langCode], () => getHomePage(langCode))

  useEffect(() => {
    if (!data) return
    setContent(data)
  }, [data])

  if (isFetching) return <></>

  return (
    <>
      <Introduce />
      <Product lstProduct={content.top_products} />
      <SpecialOffer lstPromote={content.top_promotes} />
      <Features />
      <CustomerReview lstFeedback={content.top_feedback} />
      <News lstNews={content.top_news} />
      <Contact />
      <DialogAdvertise />
    </>
  )
}
export default HomeLandingPage
